.fileinp{
    background-color: aqua;
}
.file-upload-wrapper{
    position: relative;
    width: 100%;
    height: 60px;
    cursor: pointer;
}
.file-upload-wrapper::after{
    content: attr(data-text);
    font-size:18px;
    position: absolute;
    top: 0;
    left: 0;
    background-color:#2A3038;
    padding: 10px 15px;
    display: block;
    width: calc(100%);
    pointer-events: none;
    z-index: 20;
    height: 40px;
    line-height: 40px;
    color: #999;
    border-radius:5px 10px 10px 5px;
    font-weight: 300;
}

.file-upload-wrapper::before{
    content: "Upload";
    position: absolute;
    top: 0;
    right: 0;
    display: inline-block;
    background: #0090e7;
    height: 40px;
    color: #fff;
    font-weight: 700;
    z-index: 25;
    font-size:16px;
    line-height:60px;
    padding: 0 15px;
    text-transform: uppercase;
    pointer-events: none;
    border-radius:0px 5px 5px 0px;
    transition: 0.5s ease-in-out;
}
.file-upload-field{
    /* width: 0%; */
    height: 100%;
}
.label-text{
    opacity: 0;
}